  width: 44px;
  height: 44px;
  margin-left: auto;
  padding: 22px;
  outline: 1px solid;
  border-radius: 44px;
  transition: background-color 0.3s ease;
}
.accordion__item span::after, .accordion__item span::before {
  content: "";
  position: absolute;
  display: block;
  top: 50%;
  left: 50%;
  background: currentColor;
}
.accordion__item span::before {
  transform: translate(-50%, 50%);
  width: 20px;
  height: 1px;
}
.accordion__item span::after {
  transform: translate(50%, -50%);
  width: 1px;
  height: 20px;
}
@media (hover: hover) {
  .accordion__item span:hover:not(.focus-visible) {
    outline: none;
    transform: translateY(-1px);
    transition: all 0.3s ease;
    background-color: #1c3374;
  }
  .accordion__item span:hover:not(.focus-visible)::before, .accordion__item span:hover:not(.focus-visible)::after {
    background-color: #ffffff;
  }
}
.accordion__item span.focus-visible:focus {
